@import url(https://fonts.googleapis.com/css?family=Homemade+Apple|Italianno|La+Belle+Aurore|League+Script|Meddon|Norican&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bad+Script|Pacifico|Playfair+Display|Poiret+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background-image: url("https://image.freepik.com/free-photo/old-book-open-dark-wood-background_1373-581.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#orientation {
  width: 100vw;
  text-align: center;
  margin-top: 45vh;
}

@media (orientation: portrait) {
  #orientation { display: block; }
  .interaction  { display: none; }
  }

@media (orientation: landscape) {
  #orientation  { display: none; }
  .interaction { display: block; }
}
